.categoryPage{
  .product-item:last-child {
    border-right: 1px solid #eeeeee;
    border-bottom: 1px solid #eee;
  }
  .product-show{
    margin-right: 30px;
  }

  .filterInner{
    max-height: 230px;
    overflow: hidden;
    overflow-y: scroll;
  }
  .sidebar-container{
    padding-right: 30px;
    position: relative;
  }

  .checkbox {
    margin: 0 auto;
    max-width: 100%;
    text-align: left;

    label {
      padding-left: 15px;
    }
  }
  .pull-right{margin-right: 0 !important};
  .checkbox:hover label{color: #63a4ff};
  .product-item .product-item-inner img {
    width: 100%;
    max-height: 390px;
    max-width: 170px;
    text-align: center;
    margin: 0 auto;
    float: none;
  }
  .product-img-wrap{
    a{
      margin: 0 auto;
      margin-top: 20px;
    }
  }

  .product-list-view{
    .product-img-wrap{margin: 0;}
    .product-item-inner img {
      width: 100%;
      max-height: 390px;
      max-width: 230px;
      text-align: center;
      margin: 0 auto;
      float: none;
    }
  }
  .submit_onChoose{
    position: absolute;
    right: 15%;
    top: 10%;
    z-index: 1;
    display: none;

    button{
      position: fixed;
      background: #63a4ff;
      padding: 11px 30px 10px 30px;
      color: #fff;
      border-radius: 8px 15px 15px 8px;
    }
    button:hover{
      background: #5186d0;

      .arrow-left {
        border-right: 15px solid #5186d0;
      }
    }

    .arrow-left {
      width: 0;
      left: -14px;
      top: 2px;
      position: absolute;
      height: 0;
      border-top: 15px solid transparent;
      border-bottom: 15px solid transparent;
      border-right: 15px solid #63a4ff;
      transition: all 0.3s ease 0s !important;
      -moz-transition: all 0.3s ease 0s !important;
      -webkit-transition: all 0.3s ease 0s !important;
      -o-transition: all 0.3s ease 0s !important;
    }
  }
  .showed{display: block !important;}
}

.logReg_page{
  input, input[type="text"], input[type="email"], input[type="tel"], input[type="number"], input[type="password"], input[type="radio"], input[type="checkbox"], textarea, select{
    -webkit-appearance: initial;
  }

  .form-border-box{
    padding-bottom: 20px;
    label{text-align: left};
    .form-field-wrapper{
      text-align: center;
      a{ margin-top: 30px};
    };
    .submit{
      margin-top: 10px;
    }
  }
}

.regPage{
  .form-field-wrapper.col-md-12{
    text-align: center;
    margin-top: 50px;
  }
  .form-field-wrapper{padding: 0};

  .form-group{
    float: left;
    width: 100%;
    padding: 0;

    label{text-align: left}
    input{
      padding-top: 13px;
      padding-bottom: 13px;
      height: 44px;
      width: 90%;
    }
    p{
      color: red;
      position: absolute;
      top: 3px;
      font-size: 12px;
      background: rgb(229, 229, 229);
      z-index: 12;
      padding-left: 30px;
      padding-right: 30px;
      float: none;
      margin: 0 auto;
      left: 26%;
    }
    .selectpicker{
      color: rgba(51, 51, 51, 0.77);
      outline: none;
      font-weight: 400;
      font-size: 13px;
      padding-left: 25px;
      padding-right: 25px;
      transition: all 0.2s cubic-bezier(0, 0, 0.58, 1) 0s;
      -moz-transition: all 0.2s cubic-bezier(0, 0, 0.58, 1) 0s;
      -webkit-transition: all 0.2s cubic-bezier(0, 0, 0.58, 1) 0s;
      -o-transition: all 0.2s cubic-bezier(0, 0, 0.58, 1) 0s;
    }
  }

  .btn-md{
    padding: 20px 85px !important;
    font-size: 14px;
  }
  .help-block{
    color: #f55151;
    position: absolute;
    left: 20px;
  }
  .form-horizontal{
    input, select{
      padding-top: 13px;
      padding-bottom: 13px;
      height: 44px !important;
      border-radius: 30px !important;
    }
  }
  .input-group{padding-right: 40px}
}